import React, { Component } from 'react'
import Input from './Input'
import List from './List'

class TodoList extends Component {
    constructor(props){
        super(props);
        this.state = {
            inpValue : '',
            todos : []
        }
    }
    componentDidMount(){
        // this.input.focus();
        let todos = localStorage.getItem('todos');
        if(todos){
            this.setState({
                todos: JSON.parse(todos)
            })
        }
    }
    componentDidUpdate(){
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }
    handleChange = (e)=>{
        this.setState({inpValue:e.target.value})
    }
    addTodo = ()=>{
        if(this.state.inpValue==''){
            return;
        }
        this.setState({
            todos: [
                {
                    title: this.state.inpValue,
                    done: false
                },
                ...this.state.todos
            ]
        })
        this.setState({inpValue:''})
    }
    delTodo = (idx)=>{
        // let newTodos = JSON.parse(JSON.stringify(this.state.todos))
        // newTodos.splice(idx,1);
        // this.setState({todos:newTodos});
        this.setState({
            todos: this.state.todos.filter((item,index)=>index!=idx)
        });
        // setState 是异步执行的，注意callback的用法
        // this.setState({
        //     todos: this.state.todos.filter((item,index)=>index!=idx)
        // },()=>{
        //     localStorage.setItem(
        //         'todos',
        //         JSON.stringify(this.state.todos)
        //     )
        // });
    }
    toggle = (index)=>{
        let newTodos = JSON.parse(JSON.stringify(this.state.todos))
        newTodos[index].done = !newTodos[index].done;
        this.setState({
            todos: newTodos
        })
    }
    renderTodos = (done)=>{
        return this.state.todos.map((todo,idx)=>{
            if(done==todo.done){
                return <li>
                    <input 
                        onClick={()=>this.toggle(idx)}
                        checked={done} 
                        type="checkbox"
                    />
                    <span dangerouslySetInnerHTML={{__html:todo.title}}></span>
                    <button 
                        onClick={()=>this.delTodo(idx)}
                    >
                        删除
                    </button>
                </li>
            }
        })
    }
    render() {
        const {todos} = this.state;
        let arr1 = todos.filter( item => !item.done);
        let arr2 = todos.filter( item => item.done);
        return (
            <div>
                <Input
                    inpValue ={this.state.inpValue}
                    handleChange = {this.handleChange}
                    addTodo = {this.addTodo}
                />
                <List
                    renderTodos = {this.renderTodos}
                    arr1len = {arr1.length}
                    arr2len = {arr2.length}
                />
            </div>
        )
    }
}

export default TodoList
