import React, { Component } from 'react'

class List extends Component {
    render() {
        return (
            <div>
                <h2>正在进行{this.props.arr1len}</h2>
                <ul>
                    {this.props.renderTodos(false)}
                </ul>
                <h2>已经完成{this.props.arr2len}</h2>
                <ul >
                    {this.props.renderTodos(true)}
                </ul>
            </div>
        )
    }
}

export default List
